
	import XHeader from 'vux/src/components/x-header/index.vue'

	export default{
		components : {
			XHeader
		},
		data() {
			return {
     			menu : false,
     			title : '2016上海绿色某某展览会',
     			nav : [
     				{
     					title : '展会首页',
     					href : '/'
     				},
     				{
     					title : '关于我们',
     					href : '/about'
     				},
     				{
     					title : '展商中心',
     					href : '/proList'
     				},
     				{
     					title : '展会资讯',
     					href : '/newsList'
     				},
     				{
     					title : '联系我们',
     					href : '/contact'
     				},
     				{
     					title : '在线登记',
     					href : '/register'
     				},
     			]
     		}
		},
		methods : { 
			menus (){
			  this.menu = !this.menu
		 	},
		 	headshake (ev){	
			
				ev.target.classList.add('active');
				
				setTimeout(function (){	
					ev.target.classList.remove('active');
				},750);
			
			}
		}
	}
	



