import Icon4 from '../../assets/footer_icon_05.gif'
import Icon5 from '../../assets/footer_icon_06.gif'
import Wx from '../../assets/wx.png'
import Icon from 'vux/src/components/icon/index.vue'


export default {
	components : {
		Icon
