					Icon : Icon1,
					alt : 'QQ'
				},
				{
					Icon : Icon2,
					alt : 'WX'
				},
				{
					Icon : Icon3,
					alt : 'WB'
				},
				{
					Icon : Icon4,
					alt : 'KJ'
				},
				{
					Icon : Icon5,
					alt : 'BD'
				}
			]
		}
	},
	methods : {
		popover (ev){
			
			if(ev.target.alt == 'WX'){
				this.wx = !this.wx
			}else if(ev.target.className == 'footer' || ev.target.title == '关闭'){
				this.wx = false
			}
		},
		open (ev){
